// TODO  
.example {
  background-color: $bg-grey;
}

.example__title {
  @include font-oswald;
  color: #111111;
  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
  margin-left: 20px;
  margin-right: 20px;
  padding-top: 19px;
  margin-bottom: 24px;
}

.example__story {
  margin-left: 20px;
  margin-right: 25px;
  margin-bottom: 26px;
}

.example__flex-wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 34px;
  justify-content: space-between;
}

.example__result {
  position: relative;
  width: 124px;
  border-radius: 3px;
  border: 1px solid #cdcdcd;
  text-align: center;
  padding: 14px 10px;
}

.example-result__value {
  @include font-oswald;
  color: #111111;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
}

.example-result__description {
  position: absolute;
  display: block;
  width: 87px;
  padding: 0 5px;
  background-color: $bg-grey;
  top: 45px;
  left: 16px;
  font-size: 12px;
  line-height: 12px;
}

.example__cost {
  @include font-oswald;
  color: #111111;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  margin: 0 auto;
  margin-top: 32px;
}

.example__cat-img {
  margin-bottom: 28px;
}

.example__buttons {
  position: relative;
  margin-left: 63px;
  padding-bottom: 35px;

  &::before {
    content: "";
    position: absolute;
    left: 61px;
    top: 6px;
    width: 35px;
    height: 6px;
    border-radius: 3px;
    background-color: #68b738;
    z-index: 2;
  }

  &::after {
    content: "";
    position: absolute;
    left: 55px;
    top: 3px;
    width: 82px;
    height: 12px;
    border-radius: 6px;
    border: solid 1px #cdcdcd;
    background-color: #ffffff;
    z-index: 1;
  }
}

.example__button {
  @include font-oswald;
  color: #111111;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 20px;
  border: none;
  background: transparent;
  margin: 0;
  padding: 0;

  &--before {
    margin-right: 116px;
  }
}

@media (min-width: $tablet-width) {
  .example {
    background: linear-gradient(to bottom, #ffffff 0%, #ffffff 56%, #f2f2f2 56%, #f2f2f2 100%);
  }

  .example__title {
    margin-left: 30px;
    margin-right: 30px;
    margin-top: 0;
    margin-bottom: 60px;
    font-size: 60px;
  }

  .example__story {
    margin-left: 30px;
    margin-right: 60px;
    margin-bottom: 53px;
    font-size: 16px;
    line-height: 24px;
  }

  .example__flex-wrap {
    margin-left: 30px;
    margin-right: 30px;
    margin-bottom: 85px;
    justify-content: normal;
  }

  .example__result {
    width: 162px;
    padding-bottom: 19px;
  }

  .example__result:first-child {
    margin-right: 42px;
  }

  .example-result__value {
    font-size: 30px;
    line-height: 37.02px;
  }

  .example-result__description {
    width: 137px;
    font-size: 14px;
    line-height: 14px;
    top: 62px;
    left: 12px;
    background-color: #ffffff;
  }

  .example__cost {
    width: 174px;
    text-align: right;
    font-size: 20px;
    line-height: 30px;
    margin-left: auto;
    margin-right: 0;
    margin-top: 6px;
  }

  .example__cat-img {
    display: block;
    width: 690px;
    margin: 0 auto;
    margin-bottom: 20px;
  }

  .example__button {
    font-size: 20px;
    margin-left: 35px;

    &--before {
      margin-right: 434px;
    }
  }

  .example__buttons {
    padding-bottom: 72px;

    &::before {
      content: "";
      background-color: #ffffff;
      top: -6px;
      left: 304px;
      width: 34px;
      height: 34px;
      border: 1px solid #cdcdcd;
      box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
      border-radius: 50%;
    }

    &::after {
      content: "";
      position: absolute;
      width: 8px;
      height: 8px;
      top: 7px;
      left: 317px;
      border: 4px solid #68b738;
      z-index: 2;
    }

    .example__button--before {
      &::after {
        content: "";
        position: absolute;
        width: 428px;
        height: 6px;
        background-color: #dcdcdc;
        border: 1px solid #dcdcdc;
        border-radius: 2.5px;
        top: 8px;
        left: 106px;
      }
    }
  }
}

.example__buttons:hover::before {
  cursor: pointer;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.25);
}

.example__buttons:active::before {
  cursor: pointer;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.3);
  border: 2px solid #68b738;
}

@media (min-width: $desktop-width) {
  .example {
    background: linear-gradient(to bottom, #ffffff 0%, #ffffff 22%, #f2f2f2 22%, #f2f2f2 100%);
  }

  .example .container {
    position: relative;
  }

  .example__title {
    padding-top: 0;
    margin-left: 40px;
    margin-bottom: 144px;
  }

  .example__story {
    width: 431px;
    margin-left: 40px;
    margin-bottom: 69px;
  }

  .example__flex-wrap {
    width: 430px;
    margin-left: 40px;
    margin-bottom: 0;
  }

  .example__result {
    margin-bottom: 50px;
  }

  .example-result__description {
    background-color: $bg-grey;
  }

  .example__cost {
    width: 430px;
    text-align: left;
    padding-bottom: 75px;
  }

  .example__cost span:first-child {
    margin-right: 60px;
  }

  .example__cat-img {
    position: absolute;
    top: -56px;
    right: 0;
  }

  .example__buttons {
    position: absolute;
    bottom: 6px;
    right: 40px;
  }
}
